<template>
	<!-- 挂号须知 -->
	<div>
		<div class="clear contain" v-for="(item, index) in noteList" :key="item">
			<p class="num items">{{index + 1}}.</p>
			<p class="msg items">{{item}}</p>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				noteList: [
					'自费患者可预约挂号、当日挂号；需在线支付医事服务费，支付后无需取号，直接到分诊台分诊。',
					'医保患者挂号可预约挂号，不支持当日挂号；挂号后需持医保卡和就医卡到医院自助机或挂号窗口交款取号后，再到分诊台分诊。',
					'自费患者如未办理就医卡，可以在线申请虚拟就医卡进行预约挂号，不支持当日挂号。根据北京市医保要求：医保患者必须挂医保号，如医保患者挂自费号，则诊间缴费不能用医保卡实时结算。',
					'医保患者在微信挂号之前，需到院在办卡窗口绑定医保卡和就医卡。',
					'挂号成功后，请按就诊时间提前到医院就诊；就诊单元内（上午、下午）此号有效；不按时就诊的患者，医院不再安排就诊及退费。',
					'当日挂号开放时间：上午号源7:00-10:00；下午号源7:00- 15:00。',
					'预约挂号微信退号的最迟退号时间为就诊前一天23:20；现场退号为就诊当日上午的号8:00-12:00，下午的号8:00-17:00，需持挂号条（挂号窗口取出）、就医卡和医保卡到相应科室分诊台盖章后至一层14、15号窗口办理退号。',
					'系统维护开始时间（当日）23:20；系统维护结束时间（次日） 0:30 ，在此期间无法进行微信操作。',
					'每张卡挂号限制：每天限挂两次，每个科室限挂一次，每个微信号30天内限制挂号6次。',
					'诊间支付目前暂支持非医保患者（自费、公费）。'
				]
			};
		}
	}
</script>

<style lang="less" scoped>
	@import '../common/css/global_pyt.less';
	.contain {
		padding-bottom: .3rem;
	}
	
	.items {
		float: left;
		font-size: .24rem;
		line-height: .36rem;
	}
	
	.num {
		width: .3rem;
	}
	
	.msg {
		padding-left: .15rem;
		width: calc(100% - .3rem - .3rem);
	}
</style>



