<template>
	<div id="guide">
		<!-- 就医指南 -->
		<ul class="appointmentTitle">
				<li 
				v-for="(item,index) in tabs"
				:class="{active:index == num}" 
				@click="tab(index)"><span>{{item}}</span>
				</li>
			<div class="activeLine" :style="{left: lineleft == true ? '1.4rem' : '4.85rem'}"></div>
		</ul>
		<div class="content">
			<registerNotice v-show="whitchShow"></registerNotice>
			<frequenQues v-show="!whitchShow"></frequenQues>
		</div>
	</div>
</template>

<script>
	import registerNotice from '@/components/registerNotice.vue'
	import frequenQues from '@/components/frequenQues.vue'
	
	export default {
		data() {
			return {
				tabs: ["挂号须知", "常见问题"],
				num: 0,
				lineleft: true,
				whitchShow: true
			};
		},
		components: { registerNotice, frequenQues },
		methods: {
			tab(index) {
				this.num = index;
				this.whitchShow = !this.whitchShow;
				this.lineleft = !this.lineleft;
				if(index == 0){
					this.contentAll = this.contentShowAll;
				}else if(index == 1){
					this.contentAll = this.contentWait;
				}
			},
		}
	}
</script>

<style lang="less" scoped>
	
	#guide {
		padding-bottom: 1rem;
		.appointmentTitle{
			padding: 0 0.3rem;
			box-sizing: border-box;
			height: .53*2rem;
			width: 100%;
			border-bottom: 10px solid #F6F7F8;;
			display: flex;
			background-color: #fff;
			z-index: 4;
			justify-content: space-around;
			position: fixed;
			top: .44*2rem;
			left: 0;
			li{
				width: 100%;
				height: 100%;
				line-height: .43*2rem;
				text-align: center;
				color: #272727;
				font-size: 0.16*2rem;
			}
			li.active{
				color: #3391E8;
			}
			.activeLine{
				width: .65rem*2;
				height: 2px;
				transition: all ease .3s;
				position: absolute;
				top: 0.32*2rem;
				background-color: #3391E8;
				left: .15*2rem;
			}
		}
		
		.content {
			margin-top: 2rem;
			padding: .3rem;
			box-sizing: border-box;
		}
	}
	
	
	
	
</style>



