<template>
	<!-- 常见问题 -->
	<div>
		<div v-for="item in quesList" class="listContent">
			<div class="title">{{item.title}}</div>
			<div class="msgList" v-for="val in item.msgList">
				<p>{{val}}</p>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				quesList: [
					{
						title: '一、办理出院手续时间',
						'msgList': ['1. 办理住院手续时间：', '平诊：7:30-17:00；', '周末及节假日：8:00-17:00', '急诊：全天24小时', '2. 办理出院手续时间：', '周一至周五：8:00-17:00', '周末及节假日：8:00-11:30']
					},
					{
						title: '二、办理住院手续需要准备什么？',
						'msgList': ['住院前填写好患者基本信息，住院当天持门（急）诊医生开具 的住院通知单、北医三院就医卡、患者本人身份证、社会保障卡，根据住院通知单上标注的金额带齐住院押金。  ']
					},
					{
						title: '三、患者基本信息可以通过哪些途径填写？',
						'msgList': ['1. 使用手机微信扫描住院通知单右下角二维码，关注“北医三院服务号”，绑定住院患者的就医卡，点击“诊疗”→“住院服务”→“入院登记”填写住院患者相关信息；', '2. 持患者本人身份证，通过自助机进行填写；', '3. 在门诊诊室或分诊台领取《患者情况登记表》进行填写。']
					},
					{
						title: '四、住院押金交款形式？',
						'msgList': ['现金、支票、汇票、银联卡（带有“银联”标记的借记卡或信用卡）、微信支付均可。']
					},
					{
						title: '五、单位汇款账户信息？',
						'msgList': ['收款人：北京大学第三医院', '账号：01090375700120109023052', '开户银行：北京银行学知支行', '汇款用途：注名患者姓名及所在病房', ],
					}
				]
			};
		}
	}
</script>

<style lang="less" scoped>
	.listContent {
		font-size: .24rem;
		color: #272727;
		text-align: justify;
		line-height: .38rem;
		margin-bottom: .28rem;
		.title {
			color: #3391E8;
		}
	}
</style>



